@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,400i,700,700i&subset=cyrillic");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

button {
  border-radius: 0;
}

a {
  text-decoration: none;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-family: 'Open Sans', Helvetica Neue, Helvetica, Arial, sans-serif;
  line-height: 1.4;
  font-weight: 400;
}

.title-lg {
  color: #00388a;
  font-size: 45px;
  font-weight: 400;
  line-height: 1.33333;
  text-align: center;
}

.title-md {
  color: #003085;
  font-size: 30px;
  font-weight: 400;
}

.btn {
  padding: 17px 13px;
  min-width: 230px;
  color: #1d1d1d;
  text-align: center;
  font-family: 'Open Sans', Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 22px;
  font-weight: 400;
  letter-spacing: -0.02em;
  text-transform: uppercase;
  vertical-align: top;
  transition: background-color .3s;
  border-radius: 5px;
  background-color: #fcd845;
  display: inline-block;
}

@media only screen and (max-width: 1100px) {
  .btn {
    padding: 12px 5px;
    font-size: 14px;
    min-width: 155px;
  }
}

.no-touch .btn:hover {
  background-color: #f1c204;
}

.inner {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1090px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.row {
  margin: 0 -15px;
}

.row:after {
  content: " ";
  display: table;
  clear: both;
}

.col-33 {
  float: left;
  padding: 0 30px;
  width: 33.3%;
}

.header__top {
  padding: 51px 0 10px;
  border-bottom: 1px solid #d9d9d9;
}

.header__nav {
  padding: 10px 0 0;
  border-bottom: 1px solid #d9d9d9;
}

.header__logo {
  float: left;
  margin: -30px 0 -7px;
}

.header__info {
  float: right;
  color: #787a7f;
  font-size: 18px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.logo {
  font-size: 24px;
  display: inline-block;
}

.logo:after {
  content: " ";
  display: table;
  clear: both;
}

.logo img {
  float: left;
}

.logo__text {
  float: right;
  color: #181819;
  font-weight: 400;
  margin: 22px 0 0 3px;
}

.nav {
  text-align: justify;
  line-height: 0;
  font-size: 0;
  text-justify: newspaper;
  text-align-last: justify;
}

.nav:after {
  content: "";
  display: inline-block;
  width: 100%;
  height: 0px;
  visibility: hidden;
  overflow: hidden;
}

.nav__item {
  vertical-align: top;
  display: inline-block;
}

.nav__link {
  position: relative;
  padding: 18px 6px;
  color: #003d8c;
  line-height: 1.4;
  font-size: 22px;
  display: block;
}

@media only screen and (max-width: 1100px) {
  .nav__link {
    font-size: 16px;
  }
}

.nav__link:before {
  position: absolute;
  content: "";
  left: 0;
  right: 0;
  bottom: 0;
  height: 5px;
  opacity: 0;
  transition: opacity .3s;
  background-color: #fcd845;
}

.no-touch .nav__link:hover:before {
  opacity: 1;
}

.nav__link.is-active:before {
  opacity: 1;
}

.footer {
  margin-top: 4px;
  padding: 50px 0 70px;
  border-top: 1px solid #d9d9d9;
  background-color: #f7f7f7;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.footer a {
  color: #181819;
  font-size: 24px;
  transition: border-color .3s;
  border-bottom: 1px solid transparent;
}

.no-touch .footer a:hover {
  border-color: currentColor;
}

.footer__col {
  margin-bottom: 5px;
}

.offers {
  padding-top: 10px;
}

.section__title {
  padding: 96px 0 30px;
}

.offer {
  padding: 30px 10px 58px 7px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media only screen and (max-width: 1100px) {
  .offer {
    padding: 30px 10px 38px 7px;
  }
}

.offer:not(:last-child) {
  margin-bottom: 10px;
}

.offer_gray {
  border-radius: 5px;
  background-color: #f9f9f9;
}

.offer__title {
  margin-bottom: 28px;
}

.offer__table {
  width: 100%;
  min-height: 130px;
  table-layout: fixed;
  display: table;
}

@media only screen and (max-width: 1100px) {
  .offer__table {
    min-height: 90px;
  }
}

.offer__table > div {
  vertical-align: middle;
  display: table-cell;
}

.offer__img {
  padding-left: 0;
  width: 19.2%;
}

.offer_small .offer__img {
  width: 140px;
}

.offer__img img {
  max-width: 100%;
  display: block;
}

.offer__limit {
  width: 20.9%;
}

@media only screen and (max-width: 1100px) {
  .offer__limit {
    padding-left: 25px;
  }
}

.offer_small .offer__limit {
  padding-left: 45px;
  width: 205px;
}

.offer__rate {
  width: 16.3%;
}

.offer_small .offer__rate {
  width: 175px;
}

.offer__service {
  width: 19.2%;
}

.offer_small .offer__service {
  padding-left: 45px;
}

@media only screen and (max-width: 1100px) {
  .offer_small .offer__service {
    padding-left: 15px;
  }
}

@media only screen and (max-width: 1100px) {
  .offer__service {
    padding-left: 15px;
  }
}

.offer__rate {
  padding-left: 45px;
}

@media only screen and (max-width: 1100px) {
  .offer__rate {
    padding-left: 15px;
  }
}

.offer__limit, .offer__service {
  padding-left: 55px;
}

.offer__btns {
  text-align: right;
}

.offer__text {
  color: #181819;
  font-size: 20px;
}

@media only screen and (max-width: 1100px) {
  .offer__text {
    font-size: 18px;
  }
}

.offer__numb {
  font-size: 30px;
}

@media only screen and (max-width: 1100px) {
  .offer__numb {
    font-size: 18px;
  }
}

.offer__tools {
  margin-left: 2px;
  font-size: 24px;
}

@media only screen and (max-width: 1100px) {
  .offer__tools {
    font-size: 18px;
  }
}

.offer__indicators_mod .offer__tools {
  margin-left: -5px;
}

.offer__link {
  color: #595e64;
  font-size: 14px;
  transition: border-color .3s;
  border-bottom: 1px solid;
}

.no-touch .offer__link:hover {
  border-color: transparent;
}
