.header {
	&__top {
		padding: 51px 0 10px;
		border-bottom: 1px solid #d9d9d9; }
	&__nav {
		padding: 10px 0 0;
		border-bottom: 1px solid #d9d9d9; }
	&__logo {
		float: left;
		margin: -30px 0 -7px; }
	&__info {
		float: right;
		color: #787a7f;
		font-size: 18px;
		@include font-smoothing; } }
.logo {
	font-size: 24px;
	display: inline-block;
	@include clr;
	img {
		float: left; }
	&__text {
		float: right;
		color: #181819;
		font-weight: 400;
		margin: 22px 0 0 3px; } }
.nav {
	@include justify;
	&__item {
		vertical-align: top;
		display: inline-block; }
	&__link {
		position: relative;
		padding: 18px 6px;
		color: #003d8c;
		line-height: 1.4;
		font-size: 22px;
		display: block;
		@include md {
			font-size: 16px; }
		&:before {
			position: absolute;
			content: "";
			left: 0;
			right: 0;
			bottom: 0;
			height: 5px;
			opacity: 0;
			transition: opacity .3s;
			background-color: #fcd845; }
		@include hover {
			&:before {
				opacity: 1; } }
		&.is-active {
			&:before {
				opacity: 1; } } } }
